import { toast } from 'react-toastify';

export const notificationError = () =>
  toast.error('Woops, something get wrong, try again!', {
    position: 'top-right',
    autoClose: 3000,
    hideProgressBar: false,
    closeOnClick: true,
    pauseOnHover: true,
    draggable: true,
    progress: undefined,
    theme: 'light',
  });

export const notificationExcistContact = (name: string) =>
  toast.warning(`${name}is already in contacts`, {
    position: 'top-right',
    autoClose: 3000,
    hideProgressBar: false,
    closeOnClick: true,
    pauseOnHover: true,
    draggable: true,
    progress: undefined,
    theme: 'light',
  });
